"use client";
import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import { Database, RefreshCw, ArrowLeft, Trophy } from "lucide-react";

const API_URL = "http://localhost:8080/api";

type TableData = Record<string, any>[];

export default function ViewTablePage() {
  const searchParams = useSearchParams();
  const tableName = searchParams.get("table") || "";
  
  const [data, setData] = useState<TableData>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchTableData = async () => {
    setLoading(true);
    setError(null);
    
    let endpoint = "";
    
    switch(tableName) {
      case "buyer_cash":
        endpoint = `${API_URL}/orders/buyer/cash`;
        break;
      case "buyer_crypto":
        endpoint = `${API_URL}/orders/buyer/crypto`;
        break;
      case "seller_cash":
        endpoint = `${API_URL}/orders/seller/cash`;
        break;
      case "seller_crypto":
        endpoint = `${API_URL}/orders/seller/crypto`;
        break;
      case "all":
        endpoint = `${API_URL}/orders/all`;
        break;
      // New top orders endpoints
      case "top_buyer_cash":
        endpoint = `${API_URL}/top-orders/buyer/cash`;
        break;
      case "top_buyer_crypto":
        endpoint = `${API_URL}/top-orders/buyer/crypto`;
        break;
      case "top_seller_cash":
        endpoint = `${API_URL}/top-orders/seller/cash`;
        break;
      case "top_seller_crypto":
        endpoint = `${API_URL}/top-orders/seller/crypto`;
        break;
      case "top_all":
        endpoint = `${API_URL}/top-orders/all`;
        break;
      default:
        setError("Invalid table name");
        setLoading(false);
        return;
    }
    
    try {
      const response = await fetch(endpoint);
      if (response.ok) {
        const result = await response.json();
        setData(Array.isArray(result) ? result : []);
      } else {
        setError(`Failed to fetch data: ${response.statusText}`);
      }
    } catch (err) {
      setError("Network error. Please check if backend is running.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (tableName) {
      fetchTableData();
      
      // Auto-refresh every 3 seconds for top tables
      if (tableName.startsWith("top_")) {
        const interval = setInterval(fetchTableData, 3000);
        return () => clearInterval(interval);
      }
    }
  }, [tableName]);

  const getTableTitle = () => {
    switch(tableName) {
      case "buyer_cash": return "Buyer Cash Orders";
      case "buyer_crypto": return "Buyer Crypto Orders";
      case "seller_cash": return "Seller Cash Orders";
      case "seller_crypto": return "Seller Crypto Orders";
      case "all": return "All Orders";
      // Top tables
      case "top_buyer_cash": return "Top 10 Buyer Cash (Lowest Prices)";
      case "top_buyer_crypto": return "Top 10 Buyer Crypto (Lowest Prices)";
      case "top_seller_cash": return "Top 10 Seller Cash (Highest Prices)";
      case "top_seller_crypto": return "Top 10 Seller Crypto (Highest Prices)";
      case "top_all": return "All Top 10 Orders";
      default: return "Database Table";
    }
  };

  const getTableColor = () => {
    switch(tableName) {
      case "buyer_cash": return "text-blue-600";
      case "buyer_crypto": return "text-green-600";
      case "seller_cash": return "text-purple-600";
      case "seller_crypto": return "text-orange-600";
      case "all": return "text-red-600";
      // Top tables
      case "top_buyer_cash": return "text-blue-600";
      case "top_buyer_crypto": return "text-green-600";
      case "top_seller_cash": return "text-purple-600";
      case "top_seller_crypto": return "text-orange-600";
      case "top_all": return "text-yellow-600";
      default: return "text-gray-600";
    }
  };

  const isTopTable = tableName.startsWith("top_");

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-6 flex items-center justify-between">
          <button
            onClick={() => window.close()}
            className="flex items-center gap-2 px-4 py-2 bg-white border-2 border-black rounded-lg hover:bg-gray-100 transition-all"
          >
            <ArrowLeft size={20} />
            <span className="font-semibold">Close</span>
          </button>
          
          <button
            onClick={fetchTableData}
            disabled={loading}
            className="flex items-center gap-2 px-4 py-2 bg-black text-white rounded-lg hover:bg-gray-800 transition-all disabled:opacity-50"
          >
            <RefreshCw size={20} className={loading ? "animate-spin" : ""} />
            <span className="font-semibold">Refresh</span>
          </button>
        </div>

        <div className={`bg-white rounded-3xl shadow-lg border-2 p-8 ${
          isTopTable ? "border-yellow-500 bg-gradient-to-br from-yellow-50 to-white" : "border-black"
        }`}>
          <div className="flex items-center gap-3 mb-6">
            {isTopTable ? (
              <Trophy className={`${getTableColor()}`} size={40} />
            ) : (
              <Database className={`${getTableColor()}`} size={40} />
            )}
            <h1 className="text-3xl font-bold text-black">{getTableTitle()}</h1>
            {isTopTable && (
              <span className="ml-auto px-3 py-1 bg-yellow-400 text-black text-xs font-bold rounded-full">
                AUTO-UPDATING
              </span>
            )}
          </div>

          {loading && (
            <div className="text-center py-12">
              <RefreshCw className="animate-spin mx-auto mb-4 text-gray-400" size={48} />
              <p className="text-gray-600">Loading data...</p>
            </div>
          )}

          {error && (
            <div className="bg-red-50 border-2 border-red-200 rounded-lg p-6 text-center">
              <p className="text-red-600 font-semibold">{error}</p>
            </div>
          )}

          {!loading && !error && data.length === 0 && (
            <div className="bg-gray-50 border-2 border-gray-200 rounded-lg p-12 text-center">
              <Database className="mx-auto mb-4 text-gray-300" size={64} />
              <p className="text-gray-600 text-lg font-semibold">No data found</p>
              <p className="text-gray-500 text-sm mt-2">This table is currently empty</p>
            </div>
          )}

          {!loading && !error && data.length > 0 && (
            <div className="overflow-x-auto">
              <table className="w-full border-collapse">
                <thead>
                  <tr className={`border-b-2 ${
                    isTopTable ? "bg-yellow-100 border-yellow-500" : "bg-gray-100 border-black"
                  }`}>
                    {Object.keys(data[0]).map((key) => (
                      <th
                        key={key}
                        className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider"
                      >
                        {key.replace(/_/g, ' ')}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data.map((row, idx) => (
                    <tr
                      key={idx}
                      className={`border-b border-gray-200 hover:bg-gray-300 transition-colors cursor-pointer ${
                        isTopTable
                          ? idx === 0
                            ? "bg-yellow-100 font-bold"
                            : idx < 3
                            ? "bg-yellow-50"
                            : "bg-white"
                          : idx % 2 === 0
                          ? "bg-white"
                          : "bg-gray-50"
                      }`}
                    >
                      {Object.values(row).map((value, cellIdx) => (
                        <td
                          key={cellIdx}
                          className={`px-4 py-3 text-sm ${
                            isTopTable && idx === 0 ? "text-black font-bold" : "text-gray-800"
                          }`}
                        >
                          {value !== null && value !== undefined ? String(value) : '-'}
                        </td>
                      ))}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}

          {!loading && !error && data.length > 0 && (
            <div className="mt-6 text-center text-gray-600 text-sm">
              Showing {data.length} {data.length === 1 ? 'record' : 'records'}
              {isTopTable && (
                <span className="ml-2 text-yellow-600 font-semibold">
                  (Auto-refreshes every 3 seconds)
                </span>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}